.form_input_container {
  font-weight: var(--font-4);
  font-size: var(--font-14);
  display: flex;
  flex-direction: column;
  position: relative;
}
.form_input_label {
  color: var(--color-input-label);
  margin-block: 0.6rem;
}
.form_input {
  padding-left: 10px;
  color: var(--color-input);
}

.crb {
  display: inline-block;
  position: relative;
  padding-left: 25px;
  margin-right: 15px;
  margin-bottom: 12px;
  cursor: pointer;
  width: fit-content;
}

.crb > input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.radiomark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #eee;
  border: 1px solid #aaa;
  border-radius: 50%;
}
.crb:hover input ~ .radiomark {
  background-color: #ccc;
}
.crb > input:checked ~ .radiomark {
  background-color: #2196f3;
}

.radiomark:after {
  content: "";
  position: absolute;
  display: none;
}

.crb > input:checked ~ .radiomark:after {
  display: block;
}

.crb > .radiomark:after {
  top: 5px;
  left: 5px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
}
