.form_input_container {
  font-weight: var(--font-4);
  font-size: var(--font-14);
  display: flex;
  flex-direction: column;
  position: relative;
}
.form_input_label {
  color: var(--color-input-label);
  margin-block: 0.6rem;
}
.form_input {
  padding-left: 10px;
  color: var(--color-input);
}

.ccc {
  display: inline-block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 12px;
  cursor: pointer;
  width: fit-content;
}

.ccc > input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #eee;
  border: 1px solid #aaa;
  border-radius: 3px;
}
.ccc:hover input ~ .checkmark {
  background-color: #ccc;
}
.ccc > input:checked ~ .checkmark {
  background-color: #2196f3;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.ccc > input:checked ~ .checkmark:after {
  display: block;
}

.ccc .checkmark:after {
  left: 7px;
  top: 3px;
  width: 6px;
  height: 11px;
  border: solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}
.gcci_container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}
