.fc_container {
  min-height: 270px;
  padding: 0 24px 24px 24px;
}
.fcc_section > h4 {
  width: 100%;
  font-family: var(--font-family);
  font-style: var(--font-style-normal);
  font-weight: var(--font-weight-600);
  font-size: var(--font-size-15);
  line-height: var(--line-height-17);
  color: var(--color-grey-input-text-9);
  margin-top: 12px;
}
.fccs_inner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  width: 100%;
  gap: 2%;
}
/* // for profile photo  */
.fcci_container,
.fcc_photo {
  width: 100px;
  height: 100px;
  border-radius: 12px;
}
.fcc_label {
  position: relative;
  cursor: pointer;
}
.fcce_icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.fcci_container > p {
  font-weight: 400;
  font-size: 9px;
  color: var(--color-grey-input-text-9);
  margin: 0;
  text-align: center;
}
